<template>
  <div class="center-map" ref="mapRef"></div>
</template>

<script>
export default {
  data() {
    return {
      chartRef: null,
    };
  },
  async mounted() {
    await this.$nextTick(this.initChart);
  },
  methods: {
    initChart() {
      this.chartRef = this.$echarts.init(this.$refs.mapRef);
      const option = {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
          // backgroundColor: "rgba(0, 0, 0, 0.5)", // 0 0 0 .5
          backgroundColor: "transparent", // 0 0 0 .5
          // borderColor: "#00ffcc",
          // borderWidth: 1,
          // padding: 0,
          textStyle: {
            color: "#fff",
            fontSize: 14,
          },
          // className: 'customer_map_tooltip',
          formatter(params) {
            if ("name" in params && params.name) {
              return `
                <div class="customer_map_tooltip">
                  <div class="title"><div class="title-icon"></div>${params.name }</div>
                  <p class="second_line">
                    <span class="yellow">名单数量：<a>22个</a></span>
                    <span class="yellow">客户数量：<a>22个</a></span>
                  </p>
                  <p><span class="yellow">特色产业：</span> 制造、互联网</p>
                  <p class="yellow">整体情况：</p>
                  <p class="pd-10">第一产业占比20%</p>
                  <p class="pd-10">第二产业占比30%</p>
                </div>
              `;
            }
            return `<div style="padding:5px;">${params.name}</div>`;
          },
        },
        geo: {
          map: "china",
          roam: false,
          zoom: 1.1,
          label: {
            show: true,
            color: "#00ffcc",
            fontSize: 12,
          },
          itemStyle: {
            normal: {
              areaColor: "#052b34",
              borderColor: "#00ffcc",
              shadowColor: "#00ffcc",
              shadowBlur: 15,
            },
            emphasis: {
              areaColor: "#0affb2",
            },
          },
        },
        series: [
          {
            type: "map",
            map: "china",
            roam: true,
            layoutSize: "90%",
            layoutCenter: ["50%", "90%"],
            scaleLLimt: {
              min: 1,
              max: 2,
            },
            label: {
              show: true,
            },
            select: {
              itemStyle: {
                color: "#FF0000",
              },
            },
            itemStyle: {
              normal: {
                borderColor: "#6becf5",
                color: "#fff",
              },
              emphasis: {
                areaColor: "#f60",
              },
            },
            geoIndex: 0,
            data: [
              { name: "新疆", value: 100 },
              { name: "广东", value: 80 },
              { name: "北京", value: 60 },
            ],
          },
          // 动态闪烁效果点
          {
            type: "effectScatter",
            coordinateSystem: "geo",
            rippleEffect: {
              brushType: "stroke",
            },
            itemStyle: {
              color: "#00ffcc",
            },
            symbolSize: 10,
            data: [
              {
                name: "新疆",
                value: [87.617733, 43.792818], // 乌鲁木齐经纬度
              },
            ],
          },
        ],
      };
      this.chartRef?.clear();
      this.chartRef?.setOption(option, true);
    },
  },
};
</script>

<style lang="scss" scoped>
.center-map {
  width: 100%;
  height: calc(100% - 110px);
}

::v-deep .customer_map_tooltip {
  width: 300px;
  height: 200px;
  padding: 20px !important;
  margin: 0 !important;
  background-image: url("../assets/index/map_hover_bg.png");
  background-size: cover;
  background-position: center center;

  .title {
    height: 30px;
    width: 100%;
    font-size: 20px;
    color: #f9a548;
    letter-spacing: 0;
    font-weight: bold;
    margin-bottom: 10px;
    display: flex;
    align-items: center;

    &-icon {
      width: 24px;
      height: 24px;
      background-image: url("../assets/index/warnning_icon.png");
      background-size: cover;
      background-position: center center;
      margin-right: 10px;
    }
  }

  p,
  a {
    font-size: 14px;
    color: #ffffff;
    letter-spacing: 0;
    line-height: 20px;
    font-weight: 400;

    &.second_line {
      span {
        &:last-child {
          margin-left: 30px;
        }
      }
    }

    &.pd-10 {
      padding-left: 74px;
    }
  }

  .yellow {
    color: #f9a548;
    width: 74px;
  }
}
</style>
